<template>
  <div
    v-if="openMenu"
    class="h-100vh w-full fixed left-0 top-0"
    @click="openMenu = false"
  >

  </div>
  <nav
    class="lg:left-0 <lg:pt-12 fixed top-0 h-100vh w-55 border-r-1 border-color
      dark:text-fg background z-99 transition-all flex overflow-y-auto"
    :class="{ 'left-0': openMenu, '-left-55': !openMenu }"
    @click="handleClick"
  >
    <div class="flex-grow px-4 pt-6 flex flex-col justify-between">
      <div>
        <router-link to="/user">
          <div class="flex items-center justify-between gap-2">
            <div class="text-sm text-fg-light font-medium dark:text-fg">
              {{ $store.state.auth.me?.login }}
            </div>
            <div class="w-18px h-18px
              rounded-full bg-primary">
            </div>
          </div>
        </router-link>

        <div
          class="rounded border-brd-light border-1 px-2 flex gap-2
            items-center p-1 cursor-pointer mt-4 dark:border-brd
            hover-button justify-center"
        >
          <PlusSmIcon class="w-4 h-4"/>

          <div
            class="text-fg-light dark:text-fg text-xs font-medium"
          >
            Добавить операцию
          </div>
        </div>

        <div class="mt-4 flex flex-col gap-2">
          <NavLink
            v-for="(link, index) in nav"
            :key="index"
            :to="link.to"
            :icon="link.icon"
          >
            {{ link.name }}
          </NavLink>
        </div>

        <div class="text-sm text-fg-light font-bold dark:text-fg mt-6 px-2">
          <div class="mb-2">
            <div class="flex justify-between items-center">
              <div>
                Ваши счета <span class="text-fg-secondary-light pl-1 text-xs">95,459 ₸</span>
              </div>

              <div
                @click="isCreatingAccount = true"
                class="p-0.5 border-color border-1 rounded cursor-pointer hover-button"
              >
                <PlusSmIcon
                  class="w-4 h-4"
                />
              </div>
            </div>
          </div>

          <div class="flex flex-col gap-1">
            <NavLink
              v-for="(link, index) in accounts"
              :key="index"
              :to="link.to"
              :icon="link.icon"
            >
              {{ link.name }}
            </NavLink>
          </div>
        </div>
      </div>

      <div class="flex flex-col gap-2 mt-6">
        <div
          class="flex gap-2 hover-button rounded
            items-center px-2 py-1 cursor-pointer text-fg-secondary-light"
        >
          <PlusSmIcon class="w-4 h-4"/>

          <div class="text-sm font-medium">
            Пригласить друзей
          </div>
        </div>

        <router-link to="/faq">
          <div
            class="flex gap-2 hover-button rounded
              items-center px-2 py-1 cursor-pointer text-fg-secondary-light"
          >
            <ChatIcon class="w-4 h-4"/>

            <div class="text-sm font-medium">
              Помощь и поддержка
            </div>
          </div>
        </router-link>

        <div class="w-full border-t-1px border-color my-3"></div>

        <div
          @click="logout"
          class="flex gap-2 hover-button rounded mb-6
            items-center px-2 py-1 cursor-pointer text-fg-secondary-light"
        >
          <LogoutIcon class="w-4 h-4"/>

          <div class="text-sm font-medium">
            Выйти из аккаунта
          </div>
        </div>
      </div>
    </div>
  </nav>

  <!-- Mobile bar -->
  <nav class="lg:hidden">
    <div
      @click="openMenu = !openMenu"
      class="border-1 rounded p-2 mt-4 border-color background
        flex items-center cursor-pointer fixed left-4 top-0 z-100
        hover:text-fg-light hover:dark:text-fg text-fg-secondary-light"
    >
      <MenuIcon
        class="h-4 w-4"
      />
    </div>
  </nav>

  <div class="lg:ml-55 background min-h-100vh <lg:pt-12 flex flex-col">
    <router-view/>
  </div>

  <Modal
    :visible="isCreatingAccount"
    @confirmation="handleConfirmationCreate"
  />
</template>

<script>
import {
  PlusSmIcon,
  PresentationChartLineIcon,
  CollectionIcon,
  ChartPieIcon,
  ChatIcon,
  FireIcon,
  LogoutIcon,
  MenuIcon,
  CashIcon,
  CreditCardIcon,
} from '@heroicons/vue/outline';
import Modal from '@/components/CreateAccountModal.vue';

// import { ChevronRightIcon } from '@heroicons/vue/solid';

import NavLink from '@/components/ui/NavLink.vue';

const isLink = (element) => {
  if (element.tagName === 'A') {
    return true;
  }

  if (element.parentElement) {
    return isLink(element.parentElement);
  }

  return false;
};

export default {
  components: {
    MenuIcon,
    PlusSmIcon,
    ChatIcon,
    LogoutIcon,

    Modal,
    NavLink,
  },
  data() {
    return {
      openMenu: false,
      isCreatingAccount: true,
      PlusSmIcon,
      accounts: [
        {
          name: 'Наличные',
          icon: CashIcon,
          to: '/account',
        },
        {
          name: 'Kaspi Gold',
          icon: CreditCardIcon,
          to: '/account',
        },
      ],
      nav: [
        {
          name: 'Планирование',
          icon: PresentationChartLineIcon,
          to: '/planing',
        },
        {
          name: 'Операции',
          icon: CollectionIcon,
          to: '/planing',
        },
        {
          name: 'Статистика',
          icon: ChartPieIcon,
          to: '/planing',
        },
        {
          name: 'Итоги года',
          icon: FireIcon,
          to: '/planing',
        },
      ],
    };
  },
  mounted() {
    if (!this.$store.state.auth.me) {
      this.$store.dispatch('auth/getMe');
    }
  },
  methods: {
    logout() {
      this.$store.dispatch('auth/logout')
        .then(() => {
          this.$router.push(this.$route.params.nextUrl || '/login');
        });
    },
    handleClick(event) {
      console.log(event);

      if (isLink(event.originalTarget)) {
        this.openMenu = false;
      }
    },
    handleConfirmationCreate(isConfirmed) {
      if (!isConfirmed) {
        this.isCreatingAccount = false;
      }
    },
  },
};
</script>
